'use client'
import { useState } from 'react'
import { supabaseBrowser } from '@/lib/supabaseClient'

export default function NewSubmission() {
  const [file, setFile] = useState<File | null>(null)
  const [desc, setDesc] = useState('')
  const [loading, setLoading] = useState(false)
  const supabase = supabaseBrowser()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!file || !desc.trim()) return
    setLoading(true)

    // Ensure user
    const { data: { user } } = await supabase.auth.getUser()
    if (!user) { location.href = '/(auth)/login'; return }

    // 1) Upload to storage
    const ext = file.name.split('.').pop() || 'jpg'
    const path = `${user.id}/${crypto.randomUUID()}.${ext}`
    const { error: upErr } = await supabase.storage.from('photos').upload(path, file, {
      cacheControl: '3600', upsert: false
    })
    if (upErr) { alert(upErr.message); setLoading(false); return }

    // 2) Insert DB row
    const { error: dbErr } = await supabase.from('submissions').insert({
      user_id: user.id,
      image_path: path,
      description: desc.trim()
    })
    if (dbErr) { alert(dbErr.message); setLoading(false); return }

    setDesc('')
    setFile(null)
    alert('Saved!')
    location.href = '/my'
  }

  return (
    <main className="p-6">
      <h1 className="text-xl font-bold mb-4">New Submission</h1>
      <form onSubmit={handleSubmit} className="flex flex-col gap-4">
        <input
          type="file"
          accept="image/*"
          capture="environment"
          onChange={(e) => setFile(e.target.files?.[0] ?? null)}
        />
        <textarea
          className="border rounded-xl p-3 min-h-24"
          placeholder="Write a few sentences about the photo…"
          value={desc}
          onChange={(e) => setDesc(e.target.value)}
          maxLength={2000}
        />
        <button disabled={loading} className="border rounded-xl px-4 py-2">
          {loading ? 'Uploading…' : 'Save'}
        </button>
      </form>
    </main>
  )
}
